.info {
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 65px;
  padding-bottom: 80px;

  background-color: $info-background-color;

  &__header {
    margin-left: -5px;

    font-weight: 300;
    font-size: 52px;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $font-color-dark;
    letter-spacing: 2.1px;

    @media (max-width: 1029px) {
      text-align: center;
    }
  }

  &__text-block-first {
    width: 295px;
    margin-top: 51px;

    @media (max-width: 327px) {
      width: 225px;
      height: 145px;
      margin-top: 45px;
    }
  }

  &__text-block-second {
    width: 295px;
    margin-top: 51px;

    @media (max-width: 327px) {
      width: 225px;
      height: 145px;
      margin-top: 32px;
    }
  }

  &__text-block-third {
    width: 295px;
    margin-top: 51px;

    @media (max-width: 327px) {
      width: 225px;
      height: 145px;
      margin-top: 32px;
    }
  }

  &__subscribe {
    margin-top: 71px;
  }
}

.info-container {
  max-width: 1060px;
  margin: 0 auto;
  padding-right: 30px;
  padding-left: 30px;
}

.text-blocks {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (max-width: 1116px) {
    display: flex;
    justify-content: space-around;
  }

  @media (max-width: 900px) {
    flex-direction: column;
    align-content: center;
    align-items: center;
  }
}

.text-block {
  &__header {
    font-size: 16px;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-color-dark;
    letter-spacing: 0.4px;
  }

  &__text {
    margin-top: 10px;

    font-size: 15px;
    line-height: 22px;
    font-family: "Open Sans", "Arial", sans-serif;
    text-align: left;
    color: $font-color-light-gray;
    letter-spacing: 0.3px;
  }
}
