.subscribe {
  text-align: center;

  &__header {
    font-size: 16px;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-color-dark;
    letter-spacing: 0.3px;
  }

  &__subscribe-form {
    margin-top: 26px;
  }
}

.subscribe-form {
  &__email {
    width: 326px;
    height: 40px;
    padding-right: 13px;
    padding-left: 12px;

    font-weight: 400;
    font-size: 16px;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $font-color-light-gray;
    letter-spacing: 0.7px;

    background-color: $subscribe-input-field-color;

    @media (max-width: 580px) {
      padding-left: 0;

      text-align: center;
    }

    @media (max-width: 400px) {
      width: 100%;
    }
  }

  &__subscribe-btn {
    margin-left: -4px;
  }
}
