.footer {
  max-width: 1200px;
  margin: 0 auto;

  background-color: $footer-background-color;
}

.footer-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1060px;
  margin: 0 auto;
  padding-top: 29px;
  padding-right: 30px;
  padding-bottom: 22px;
  padding-left: 30px;

  @media (max-width: 1020px) {
    flex-direction: column;
    align-items: center;
  }
}

.copyright {
  font-size: 14px;
  line-height: 22px;
  font-family: "Open Sans", "Arial", sans-serif;
  text-align: center;
  color: $font-color-light-gray;
  letter-spacing: 0.3px;
}

.footer-nav {
  width: 100%;
  max-width: 327px;

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    list-style-type: none;

    @media (max-width: 1020px) {
      justify-content: space-around;
      padding-top: 10px;

      text-align: center;
    }
  }

  &__item {
    font-size: 14px;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-color-dark;
    letter-spacing: 0.4px;

    &:first-child {
      @media (max-width: 1020px) {
        margin-left: 0;
      }
    }
  }

  &__link {
    color: $font-color-dark;
    text-decoration: none;
  }
}
