.intro {
  max-width: 1200px;
  margin: 0 auto;

  background: url("../img/intro-bg.jpg") no-repeat;
  background-size: cover;
}

.intro-container {
  max-width: 1060px;
  margin: 0 auto;
  padding-right: 30px;
  padding-left: 30px;
}

.header {
  display: flex;
  justify-content: space-between;
  padding-top: 17px;

  &__menu-btn {
    margin-top: 15px;
  }
}

.logo {
  display: inline-block;

  text-decoration: none;

  &__img {
    display: inline-block;
    margin-bottom: -2px;
  }

  &__title {
    display: inline-block;
    margin-left: 9px;

    font-weight: 300;
    font-size: 32px;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $font-color-white;
    letter-spacing: 1.4px;
  }
}

.search {
  padding-top: 198px;
  padding-bottom: 206px;

  text-align: center;

  &__header {
    font-weight: 300;
    font-size: 52px;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $font-color-white;
    letter-spacing: 2.1px;
  }

  &__search-form {
    margin-top: 25px;
  }
}

.search-form {
  &__field {
    width: 326px;
    height: 40px;
    padding-right: 10px;
    padding-left: 15px;

    font-weight: 400;
    font-size: 16px;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $font-color-light-gray;
    letter-spacing: 0.5px;

    @media (max-width: 540px) {
      text-align: center;
    }

    @media (max-width: 400px) {
      width: 100%;
    }
  }

  &__search-btn {
    margin-left: -4px;
  }
}
