.top-destinations {
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 56px;
  padding-bottom: 68px;

  background-color: $top-destinations-background-color;
  border-bottom: 1px solid $top-destinations-border-color;

  &__header {
    font-size: 16px;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-color-dark;
    letter-spacing: 0.3px;

    @media (max-width: 575px) {
      text-align: center;
    }
  }

  &__subheader {
    margin-top: 6px;

    font-weight: 400;
    font-size: 15px;
    font-family: "Open Sans", "Arial", sans-serif;
    color: $font-color-light-gray;
    letter-spacing: 0.3px;

    @media (max-width: 575px) {
      text-align: center;
    }
  }

  &__cards {
    margin-top: 36px;
  }
}

.top-destinations-container {
  max-width: 1060px;
  margin: 0 auto;
  padding-right: 30px;
  padding-left: 30px;
}

.destination-card-link {
  text-decoration: none;
}

.top-destinations-cards {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (max-width: 1136px) {
    justify-content: space-around;
  }
}

.destination-card {
  width: 240px;
  height: 319px;

  background-color: $destination-card-color;
  border-bottom: 2px solid $destination-card-border-color;

  transition: border-bottom-color 0.5s;

  &:hover {
    border-bottom-color: $destination-card-border-hover-color;

    transition: border-bottom-color 0.5s;
  }

  @media (max-width: 1136px) {
    margin-bottom: 30px;
  }

  &__price {
    position: absolute;
    right: 13px;
    bottom: 13px;

    font-size: 12px;
    font-family: "Montserrat", "Arial", sans-serif;
    color: $font-color-white;
    letter-spacing: 1px;
  }

  &__text {
    width: 205px;
    margin: 0 auto;
  }

  &__header {
    padding-top: 16px;
    padding-bottom: 13px;

    font-size: 13px;
    font-family: "Montserrat", "Arial", sans-serif;
    text-align: left;
    color: $font-color-dark;
    letter-spacing: 0.97px;
  }

  &__description {
    font-size: 13px;
    line-height: 19px;
    font-family: "Open Sans", "Arial", sans-serif;
    text-align: left;
    color: $font-color-gray;
    letter-spacing: 0;
  }

  &__picture {
    position: relative;

    display: block;
    width: 240px;
    height: 200px;
  }
}
